
import { defineComponent, ref } from 'vue'
import Countries from '@/assets/countries/world.json'
import { QuickFetch } from '@/components/quick'

export default defineComponent({
    setup() {
        const nationality = ref('twn');
        const qf = new QuickFetch();
        const country_list = Countries;
        const consent: any = ref([]);
        const q1: any = ref(0);
        const q1_input: any = ref('');
        const q2: any = ref([]);
        const q2_input: any = ref('');
        const q3: any = ref(0);
        const q4: any = ref(0);
        const q5: any = ref(0);
        const q6: any = ref(0);
        const q6_input: any = ref('');
        function submitAll(){
            const r = confirm('送出即無法修改，確定送出？\nAfter you submit, you will not be able to modify this form.\nAre you sure you want to submit?')
            if(r){
                if(consent.value.length != 2) {
                    alert("請勾選同意個資使用及個人聲明法律責任");
                }else if(q1.value=='Y' && q1_input.value.length==0){
                    alert("請填寫第一題");
                }else if(q6.value=='Y' && q6_input.value.length==0){
                    alert("請填寫第六題");
                }else{
                    if(q2.value.length==0){
                        q2.value = '0';
                    }
                    if(q2_input.value.length>0){
                        const oth_idx = q2.value.indexOf('other');
                        q2.value[oth_idx] = q2_input.value;
                    }
                    qf.Dataset().AddObj('nationality',nationality).AddObj('q1',q1.value=='0'? q1:q1_input).Add('q2',JSON.stringify(q2.value)).AddObj('q3',q3).AddObj('q4',q4).AddObj('q5',q5).AddObj('q6',q6.value=='0'? q6:q6_input);
                    qf.Url('nhc/form').Post(qf.GetDset()).then((res: any)=>{
                        if(res.message=='done'){
                            alert('已提交 Done');
                        }else if(res.message=='has_submit'){
                            alert('無法重複提交 Unable to resubmit');
                        }else{
                            alert('提交失敗，請洽詢體育組 Failed, Please contact Physical & Health Division');
                        }
                    });
                }
            }
        }
        return {
            nationality,
            country_list,
            consent,
            q1,
            q1_input,
            q2,
            q2_input,
            q3,
            q4,
            q5,
            q6,
            q6_input,
            submitAll,
        }
    },
})
